import Vue from "vue";
import Component from "vue-class-component";

@Component
export class TagHelper extends Vue {
  createTag() {
    const name = window.prompt("请输入标签名")?.trim();
    if (name === "") window.alert("标签名不能为空");
    if (!name) return;
    this.$store.commit("createTag", name);
  }
}
